.mobile-header {
  .mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.m-menu-drawer {
  .ant-drawer-content-wrapper {
    width: 100% !important;

    .ant-drawer-content {
      background: #f0f2f7;
    }
  }

  .connect-wallet {
    .connect-btn {
      display: inline-block;
      padding: 15px;
      width: 95%;
      text-align: center;
    }
  }

  .lg-content {
    position: absolute;
    bottom: 30px;
  }

  .account-basic-info-m {
    width: 95%;
    font-size: 14px;
    margin-top: 40%;

    .address-text {
      margin-top: 30px;
      height: 40px;
      line-height: 40px;
      justify-content: center;
    }
  }

  .logo {
    margin-bottom: 20px;
  }

  img.closeIconMobile {
    width: 14px;
  }
}

.ant-menu-inline .ant-menu-item:not(:last-child) {
  margin-bottom: 20px;
}

.ant-menu-inline .ant-menu-item {
  margin-top: 20px;
}

.mobile-menu {
  padding-top: 40%;
}

@media screen and (max-width: 1000px) {
  .header-container {
    padding: 0;

    .header-content {
      padding: 15px;
      height: auto;

      .logo img {
        width: 55px;
      }
    }
  }

  .custom-modal {
    top: calc(50vh - 160px);
  }

  .m-menu-drawer {
    .white-content {
      margin-top: 30px;

      a {
        color: #fff;
        font-size: 14px;
      }
    }
  }

  .m-menu-drawer .account-basic-info-m {
    margin-top: 0;

    .trx-balance {
      margin-right: 0;
      width: 100%;
      margin-top: -100px;
      height: 40px;
      line-height: 40px;
    }

    .address-text {
      margin-right: 0;
      width: 100%;
      margin-top: 0;
    }

    .white-content {
      margin-top: 30px;

      a {
        color: #fff;
      }
    }
  }

  .mobile-menu {
    padding: 0;

    .connect-wallet {
      text-align: center;
      min-height: calc(100vh - 48px);
      width: 80%;
      margin: 0 auto;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;

      a {
        color: #ffffff;
        padding: 0px 30px;
        font-family: HelveticaNeue;
        font-size: 14px;
        height: 40px;
        line-height: 40px;
        margin-top: 20px;

        &.active {
          color: #6726eb;
        }
      }

      .connect-btn {
        margin-right: 0;
        margin-top: 50%;
        width: 100%;
      }
    }
  }

  .m-menu-drawer .lg-content {
    left: 0;
    right: 0;

    .lg-mobile {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #ffffff;
      letter-spacing: 0.6px;
      text-align: center;
      line-height: 20px;

      .slice-lg {
        color: #fff;
      }

      .lg-active {
        color: #3d56d6;
      }
    }
  }
}

.horizontal-line {
  display: block;
  width: 100%;
  height: 1px;
  background: rgba(15, 19, 79, 0.05);
  margin: 20px 0;

  &.menu {
    background: rgba(15, 19, 79, 0.05);
  }

  &.title {
    background: #f2f2f2;
  }
}

#root {
  position: relative;
  min-height: calc(100vh);
  // padding-bottom: 30px;
  background: #f1f2f7;
}

.right-container {
  padding: 20px;
  transition: all ease-in 0.2s;
  width: calc(100% - 210px);

  &.max-width {
    width: calc(100% - 100px);
  }
}

.tooltip-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(../images/tooltipIcon.svg) 0 0 no-repeat;
}

.ant-modal-header {
  border: none;

  .ant-modal-title {
    color: #0f134f;
    font-size: 16px;
    text-align: center;
  }
}

.ant-modal-header {
  border-radius: 8px 8px 0 0;
}

.ant-modal-body {
  border-radius: 0 0 8px 8px;
  padding-top: 0;
}

.ant-modal-content {
  border-radius: 8px;
}

.ant-tabs-card > .ant-tabs-nav .ant-tabs-tab {
  border: none;
  margin: 0;
  border-bottom: 1px solid #f2f2f2;
  background-color: #fff;
}

.trans-modal-title {
  height: 60px;
  line-height: 60px;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  color: #0f134f;
}

.trans-modal-wait-confirm,
.trans-modal-submit-tips > a,
.trans-modal-cancel {
  font-size: 12px;
  line-height: 17px;
  color: #0f134f;
  margin: 20px 0 10px;
}

.trans-modal-submit-tips,
.trans-modal-cancel {
  margin: 20px 0;
}

.mobile-header {
  padding: 12px 15px 16px;
  margin-bottom: -45px;
  color: #fff;
  background: #3e54cb;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9;

  .intro-container {
    height: 150px;
  }

  .mobile-logo {
    > span {
      display: inline-block;
      width: 32px;
      height: 28px;
      background: url(../images/mobile/logo.svg) 0 0 no-repeat;
      background-size: 100%;
    }
  }

  .mobile-category {
    position: relative;
    top: 3px;
    width: 16px;
    height: 12px;
    background: url(../images/menu.svg) 0 0 no-repeat;
    background-size: 100%;
    &:hover {
      opacity: 0.5;
    }
  }
  .copy-img {
    display: inline-block;
    width: 12px;
    height: 12px;
    background: url(../images/mobile/copy.svg) 0 0 no-repeat;
    background-size: 100%;
  }

  &.transparant-bg {
    color: #0f134f;
    background: #eff1f6;
    .mobile-logo {
      > span {
        background-image: url(../images/mobile/logo-new.svg);
      }
    }
    .wallet-info {
      border: 1px solid rgba(15, 19, 79, 0.1);
      > span {
        color: #0f134f;
      }
      &:hover {
        border: 1px solid rgba(15, 19, 79, 1);
      }
    }
    .copy-img {
      background-image: url(../images/mobile/copy-black.svg);
    }
    .mobile-category {
      height: 15px;
      background-image: url(../images/mobile/menu-black.svg);
    }
  }
}

.mobile-link {
  display: block;
  height: 50px;

  > span {
    display: block;
    line-height: 30px;
    color: #0f134f;
    text-align: left;
    font-size: 14px;
    font-weight: normal;
    font-family: PingFang SC;
  }

  &.logos {
    padding-left: 0 !important;
    margin-bottom: 5px;

    &.liquidity-logo .new-icon {
      left: 15px;
      top: 0px;
    }
  }

  &.small-logo {
    margin-bottom: -10px;
    margin-left: 6px;

    > span:first-child {
      margin-top: 14px;
      background-size: 20px;
    }

    > span:last-child {
      margin-left: 0;
    }

    &.help-logo {
      > span:last-child {
        margin-left: 4px;
      }
    }
  }

  &.big-logo > span:first-child {
    width: 30px;
    height: 30px;
    background-size: 100%;
    margin-top: 9px;
  }
  .mobile-twitter-logo,
  .mobile-telegram-logo {
    opacity: 0.5;
  }
  &:hover {
    .mobile-twitter-logo,
    .mobile-telegram-logo {
      opacity: 1;
    }
  }
}

#m-active-menu-text {
  font-weight: 600;
  color: #3d56d6;
  font-size: 15px;
}

.mobile-language-link span {
  font-size: 14px !important;
}

.fs14 {
  font-size: 14px !important;
}

.mobile-menu-drawer {
  .ant-drawer-content-wrapper {
    background-color: #000;
  }

  .ant-drawer-close {
    padding: 35px 20px;
  }

  .ant-drawer-mask {
    background: rgba(15, 19, 79, 0.7);
  }

  .menu-content {
    .connect-wallet {
      margin-top: 30px;
      margin-bottom: 15px;
    }

    .horizontal-line {
      width: calc(100% + 30px);
      margin: 13px 0 7px;
    }

    .mobile-drawer-footer {
      position: absolute;
      left: 30px;
      top: calc(100% - 75px);

      .mobile-link {
        margin-top: 20px;
        display: inline-block;

        > span {
          display: inline-block;
          font-size: 14px;
        }

        &.footer-logo {
          margin-right: 24px;

          &:last-child {
            margin: 0;
          }

          .mobile-twitter-logo,
          .mobile-telegram-logo,
          .mobile-github-logo {
            width: 15px;
            height: 15px;
            background: url(../images/mobileTwitterLogo.svg) 0 0 no-repeat;
            background-size: 100%;
            margin-right: 7px;
            position: relative;
            top: 3px;
          }

          .mobile-telegram-logo {
            background-image: url(../images/mobileTelegramLogo.svg);
          }

          .mobile-github-logo {
            background-image: url(../images/github.svg);
            background-size: 15px 15px;
          }
        }
      }
    }

    .lg-active {
      color: #3d56d6;
      font-weight: 500;
    }

    .mobile-drawer-logo {
      margin-top: 30px;
      margin-bottom: 50px;

      > img:first-child {
        margin-right: 6px;
      }
    }
  }

  .slice-lg {
    color: #a1a1a1;
  }

  .ant-drawer-content-wrapper {
    width: 66% !important;
  }

  .ant-drawer-body {
    padding: 0 30px 12px;
    position: relative;
  }

  .mobile-address-text {
    display: inline-block;
    min-width: 130px;
    padding: 14px 18px;
    border-radius: 4px;
    font-family: PingFang SC;
    background: #3d56d6;

    > div {
      align-items: center;

      img {
        width: 14px;
        height: 14px;
        margin-top: 4px;
      }

      span {
        display: block;
        color: #fff;
        font-size: 14px;
        margin-left: 13px;
      }
    }
  }
}

button:hover,
button:active,
button:link,
button:visited,
button:focus {
  outline: 0;
}

.ant-tooltip {
  padding-top: 0;
  max-width: 400px;

  // &.modal-tooltip {
  //   padding-bottom: 0;

  //   .ant-tooltip-arrow {
  //     background: url(../images/bottomTriangle.svg) 0 0 no-repeat;
  //     background-size: 100%;
  //   }
  // }

  // .ant-tooltip-arrow {
  //   width: 15px;
  //   height: 6px;
  //   background: url(../images/topTriangle.svg) 0 2px no-repeat;
  //   background-size: 100%;

  //   .ant-tooltip-arrow-content {
  //     display: none;
  //   }
  // }
  .ant-tooltip-arrow-content {
    background: #0f134f;
  }
  .ant-tooltip-inner {
    min-width: 100px;
    padding: 16px 12px;
    border-radius: 8px;
    background-color: #0f134f;
    font-size: 12px;

    a {
      font-size: 12px;
    }

    > span {
      font-size: 12px;
    }

    > div {
      margin-top: 12px;
      font-size: 12px;
      color: #fff;
      text-align: center;
      cursor: pointer;

      &:nth-child(1) {
        font-size: 14px;
        font-weight: 600;
      }

      &:last-child {
        margin-bottom: 10px;
      }
    }

    .info-tooltip {
      text-align: left;
      margin: 0 !important;

      div {
        font-size: 12px;
        font-weight: 400;
      }
    }
