}

#userList-container .ant-tooltip .ant-tooltip-arrow {
  background: url(../images/bottomTriangle.svg) no-repeat;
}

#userList-container .ant-tooltip-placement-top {
  padding-bottom: 0;
}

.ant-tabs-top > .ant-tabs-nav {
  margin-bottom: 3px;

  &::before {
    border: none;
  }
}

.ant-modal-close-x {
  position: absolute;
  right: 16px;
  top: 16px;
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url(../images/mobile/close-icon.svg) center no-repeat;
  background-size: 12px 12px;

  .anticon > svg {
    display: none;
  }
}

.ant-modal {
  top: calc(50% - 220px);

  &.vote-deposit,
  &.login-modal {
    top: calc(50% - 140px);
  }

  &.borrow-modal {
    top: calc(50% - 250px);
  }

  &.mortgage-modal {
    top: calc(50% - 188px);
  }

  p {
    line-height: 14px;
  }

  &.DAWPop,
  &.borrow-modal {
    .ant-tabs-nav {
      width: calc(100% + 48px);
      transform: translateX(-24px);
    }

    .ant-tabs {
      overflow: visible;
    }
  }
}

.ant-input-affix-wrapper {
  height: 40px;
  line-height: 40px;
}

.ant-input,
.ant-input-affix-wrapper {
  font-weight: 500;
  border: 1px solid #d9d9d9;
  box-shadow: none;

  &:hover,
  &:focus {
    border: 1px solid #d9d9d9;
    box-shadow: none;
  }
}

::-webkit-input-placeholder {
  /* WebKit browsers */
  font-weight: 200;
  color: #545669;
}

.ant-btn-primary {
  text-shadow: none;
}

.angle-right {
  display: inline-block;
  width: 6px;
  height: 11px;
  background: url(../images/angleRight.svg) 0 0 no-repeat;
  background-size: 100%;
  margin-left: 12px;
}

.ant-table-thead > tr > th {
  > span {
    font-size: 12px;
  }
}

.info-icon {
  position: relative;
  top: 8px;
  left: 0;
  vertical-align: top;
  padding: 5px 2px;

  &.t3 {
    top: 3px;
  }
}

div.mobile-right-container {
  .nav-container .intro-container {
    height: auto !important;

    .intro-text {
      top: 30px;
      left: 20px;
      transform: none;
      width: 170px;

      &.english-text {
        width: 197px;
      }

      .intro-title {
        margin-bottom: 8px;
      }
    }
  }

  .nav-container .intro-container .intro-text .intro-subtitle {
    line-height: 15px;

    > span,
    > span > span {
      font-size: 12px;
    }
  }

  .nav-container .intro-container .intro-text .intro-title > span:first-child {
    width: 75px;
    height: 17px;
  }

  .infoList-container,
  .market-summary-list {
    padding-left: 10px;
