  }

  .nav-container .intro-container,
  .infoList-container,
  .market-summary-list,
  .go-transcan,
  .progress-info,
  .vote-detail-info,
  .vote-detail-timeline {
    margin-top: 15px !important;
  }

  .infoList-container {
    margin-top: 19px !important;
  }

  .market-summary-blocks .market-summary-block:first-child,
  .market-detail-header,
  .market-detail-info,
  .vote-header,
  .vote-header h1,
  .vote-left,
  .vr-info {
    margin-bottom: 15px !important;
  }

  .ant-modal {
    width: calc(100% - 60px) !important;
  }
