.ant-input-affix-wrapper {
  overflow: hidden;
}

.ant-table-thead > tr > th {
  font-weight: 400 !important;
  border-bottom: none;
}

.ant-table-tbody > tr.ant-table-row:hover > td {
  background-color: #f5f7fd;
}

.ant-btn-primary {
  box-shadow: none;
}

.ant-select:focus,
.ant-select-open,
.ant-select-focused,
.ant-select:not(.ant-select-disabled):focus {
  outline: none;
  box-shadow: none;
}

.ant-select:hover .ant-select-selector,
.ant-select:not(.ant-select-disabled):hover .ant-select-selector,
.ant-select-focused:not(.ant-select-disabled).ant-select:not(.ant-select-customize-input) .ant-select-selector {
  border-color: #d9d9d9;
  box-shadow: none;
}

.ant-select-selector {
  box-shadow: none;
}

.collateralSymbol {
  position: relative;
}

.pink-icon,
.blue-icon {
  position: absolute;
  left: 106px;
  display: inline-block;
  line-height: 21px;
  padding: 0 8px;
  border-radius: 64px;
  margin-top: -20px;
}

.pink-icon {
  color: #d146a5;
  background: rgba(209, 70, 165, 0.1);
}

.blue-icon {
  color: #3d56d6;
  background: rgba(61, 86, 214, 0.1);
}

.new-menu {
  display: flex;
  align-items: center;

  &.language-logo {
    .ant-select {
      &:hover {
        .ant-select-selector {
          background-image: url(../images/enLogoHover.svg) !important;
        }

        .en-text,
        .zh-text,
        .slice-lg {
          font-size: 12px !important;
          font-weight: 400 !important;
        }
      }
      .ant-select-selector {
        padding-left: 33px !important;
        background: url(../images/enLogo.svg) 0 4px no-repeat !important;
      }

      &.ant-select-open {
        .ant-select-arrow {
          // transform: rotate(180deg);
          background-image: url(../images/mobile/arrow-top-blue.svg);
        }
      }
    }

    &.ch-logo {
      .ant-select-selector {
        background-image: url(../images/chLogo.svg) !important;
        &:hover {
          background-image: url(../images/chLogoHover.svg) !important;
        }
      }
    }
  }

  &.mobile-resources {
    .ant-select {
      &:hover {
        .ant-select-selector {
          background-image: url(../images/menu/resourcesHover.svg) !important;
        }
      }
      .ant-select-selector {
        background: url(../images/menu/resources.svg) 0 4px no-repeat !important;
      }
    }
  }

  &.mobile-developer {
    .ant-select {
      &:hover {
        .ant-select-selector {
          background-image: url(../images/menu/developerHover.svg) !important;
        }
      }
      .ant-select-selector {
        background: url(../images/menu/developer.svg) 0 5px no-repeat !important;
      }
    }
  }

  .ant-select {
    display: inline-block;
    margin-top: 9px;
  }

  .ant-select-selector {
    border: none !important;
    background: transparent !important;
    padding: 0 !important;
  }

  .ant-select-selection-item {
    font-size: 16px;
    // color: #333 !important;
  }

  &:hover {
    .ant-select-selection-item {
      color: #3d56d6 !important;
    }
  }

  .ant-select-arrow {
    color: #333;
    top: 55%;
  }

  .ant-select-arrow .anticon > svg {
    width: 16px;
    height: 10px;
  }

  .ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    border: none;
    box-shadow: none;
  }

  .ant-select-single.ant-select-open .ant-select-selection-item {
    opacity: 1;
  }

  .ant-select-arrow {
    width: 8px;
    height: 5px;
    background: url(../images/mobile/arrow-down-black.svg) 0 0 no-repeat;
    background-size: 100%;
    right: 20px;

    svg {
      display: none;
    }
  }

  .ant-select:not(.ant-select-customize-input) .ant-select-selector {
    border: none;
  }
}

.minWid {
  .new-menu.language-logo {
    .ant-select {
      width: auto !important;
    }
    .ant-select-arrow {
      display: none;
    }
  }
}

.logos:hover .ant-select span {
  font-size: 12px;
}

.lang-select {
  width: 100px !important;
  min-width: 100px !important;
  background: #ffffff;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  .ant-select-item {
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    color: #0f134f;
  }
  &.ant-select-dropdown {
    padding: 0;
  }

  .ant-select-item-option-content {
    color: #333;
    font-size: 12px;
    text-align: center;
  }

  .ant-select-item-option-active:not(.ant-select-item-option-disabled),
  .ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
    background: #e0e8ff;

    .ant-select-item-option-content {
      color: #3d56d6;
      font-weight: 400;
    }
  }
}

.infoList-container,
.market-summary-list {
  .ant-table-column-title {
    font-size: 12px;
    > span {
      font-size: 12px;
    }
  }
  .ant-table-column-sorter-up.active,
  .ant-table-column-sorter-down.active {
    color: #0f134f;
  }
  .ant-table-column-sorter {
    margin-left: 4px;
  }
  .ant-table-column-sorter-inner {
    transform: translateY(3px);
  }
  .ant-table-column-sorter-up,
  .ant-table-column-sorter-down {
    font-size: 24px;
    > svg {
      width: 10px;
    }
  }
}

.market-summary-list .ant-table-cell-content {
  overflow: visible !important;
}

.show-all-markets {
  display: flex;
  align-items: center;
  .ant-checkbox {
    top: 0;
    left: 4px;
    width: 12px;
    height: 12px;
    border: 1px solid #0f134f;
    border-radius: 4px;
  }
  .ant-checkbox-inner {
    width: 10px;
    height: 10px;
    border: none;
    &::after {
      width: 4px;
      height: 7px;
    }
  }
  // .ant-checkbox-checked .ant-checkbox-inner {
  //   background: #0f134f;
  // }
  .ant-checkbox-checked::after {
    border: none;
  }
  .show-all-markets-m {
    .ant-checkbox-inner {
      width: 12px;
      height: 12px;
    }
  }
}

@media screen and (max-width: 1000px) {
  .mobile-right-container {
    width: 100%;
    padding: 60px 15px 20px;
  }

  .info-icon {
    top: 2px;
  }

  .pink-icon,
  .blue-icon {
    left: 5px;
    top: 5px;
